import React from 'react'
import MerginHeight from '../../comps/MerginHeight'
import ProgressComp from '../../comps/ProgressComp'

class Comp extends React.PureComponent {
  constructor(props) {
    super(props)
  }
  render() {
    let imgH = 120
    return (
      <div className='container-fluid' >
        <MerginHeight h={10} ></MerginHeight>
        <div className='row' style={{
          height: 50,
          lineHeight: '50px',
        }} >
          <span style={{ marginLeft: 10, color: '#35B69E', fontSize: 18 }} >GDP</span><span style={{ marginLeft: 20, fontSize: 12, fontWeight: 300, color: '#35b69e', opacity: 0.8 }} >BRIEF INTRODUCTION</span>
        </div>
        <div className='row' style={{
          height: 1, background: '#fff',
          boxShadow:'0px 1px 0px #35B69E',
        }} ></div>
        <div className='row' style={{
          paddingLeft: '10px',
          paddingRight:'10px',
          marginTop:'10px'
        }} >
          <MerginHeight h={10} ></MerginHeight>
          <p className='p' >Genome Decode ProgramGDP1027ICG-12WGSWhole genome resequencing</p>
        </div>
        <div className='row' style={{
          transform:'translate(-150px, 40px)'
        }} >
        <ProgressComp top={51}></ProgressComp>
        </div>
      </div>
    )
  }
}
export default Comp


