import React from 'react'
import Input from '../../comps/Input'
import api from '../../model/api'

let vmInput = function(self, key, e, num) {
  if (e && self && key) {
    num = num === undefined ? 999 : num
    var value = e.target.value
    if (value.length > num) {
      value = value.slice(0, num)
    }
    self.setState(function(state) {
      state[key] = value
    })
  }
}

let regPhone = function(str) {
  var reg = /1[3,4,5,6,7,8,9]\d{9}/
  if (str.match(reg) === null) {
    return false
  } else {
    return true
  }
}

let regCode = function(str) {
  var reg = /\d{4}/
  if (str.match(reg) === null) {
    return false
  } else {
    return true
  }
}

let regFont = function(str) {
  var regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,
    regCn = /[#|[\]]/im
  if (regEn.test(str) || regCn.test(str)) {
    return ''
  } else {
    return true
  }
}


class Comp extends React.PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      btnVin: false,
      name: '',
      danwei: '',
      zhiwei: '',
      ketizhu: '',
      mail: '',
      phone: '',
      address: '',
      server: '',
      simple: '',
      question: '',
    }
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="row">
          <div
            className="col-md-12"
            style={{
              textAlign: 'center',
              height: 80,
              marginTop: 10,
              lineHeight: '80px',
              color: '#35B69E',
              fontSize: 22,
              fontWeight: 400
            }}>
            GDP
          </div>
        </div>
        <div
          className="row"
          style={{
            color: '#6E6E6E',
            marginTop: 10,
            fontSize: 13
          }}>
          <div className="col-md-1" />
          <div className="col-md-10">
            <div className="row">,</div>
            <div
              className="row"
              style={{
                marginTop: 10,
                textIndent: '2em'
              }}>
              <p>
                GDP
              </p>
            </div>
          </div>
          <div className="col-md-1" />
        </div>
        <div className="row" style={{marginTop:40, marginLeft:0}}>
            <div className="col-md-6" style={{textAlign:'center'}} >
            <Input onChange={(e) => { vmInput(this, 'name', e)}} title="" def="" />
            </div>
            <div className="col-md-6" style={{textAlign:'center'}} >
              <Input  onChange={(e)=>{vmInput(this, 'danwei', e)}} title="" def="" />
            </div>
          </div>
          <div className="row" style={{marginTop:15, marginLeft:0}} >
            <div className="col-md-6" style={{textAlign:'center'}} >
              <Input  onChange={(e)=>{vmInput(this, 'zhiwei', e)}} title="" def="" />
            </div>
            <div className="col-md-6" style={{textAlign:'center'}} >
              <Input onChange={(e)=>{vmInput(this, 'ketizhu', e)}} title=":" def="" />
            </div>
          </div>
          <div className="row" style={{marginTop:15, marginLeft:0}}>
            <div className="col-md-6" style={{textAlign:'center'}} >
              <Input onChange={(e)=>{vmInput(this, 'mail', e)}} title="" def="" />
            </div>
            <div className="col-md-6" style={{textAlign:'center'}} >
              <Input onChange={(e)=>{vmInput(this, 'phone', e)}} title="" def="" />
            </div>
          </div>
          <div className='row' style={{ marginTop: 15,marginLeft:16 }} >
            <Input onChange={(e)=>{vmInput(this, 'address', e)}} className='col-md-12' long title=":" def="" />
        </div>
        <div className='row' style={{ marginTop: 15, marginLeft:16}} >
            <Input onChange={(e)=>{vmInput(this, 'server', e)}} className='col-md-12' long title=":" def="" />
        </div>
        <div className='row' style={{ marginTop: 15,marginLeft:16 }} >
            <Input onChange={(e)=>{vmInput(this, 'simple', e)}} className='col-md-12' long title=":" def="" />
        </div>
        <div className='row' style={{ marginTop: 15,marginLeft:16 }} >
            <Input onChange={(e)=>{vmInput(this, 'question', e)}} className='col-md-12' long title=":" def="" />
        </div>
        <div className='row' style={{ textAlign: 'center', height:170 }} >
          <div onClick={this.send} className='mbtn' onMouseEnter={this.btnVin} onMouseLeave={this.btnVout} style={{
            margin: '55px auto',
            height: 50, width: 200, background:this.state.btnVin?'#21927D': '#35B69E', color: '#fff',
            lineHeight: '50px',
          }}  ></div>  
        </div>
      </div>
    )
  }
  btnVin = () => {
    this.setState({
      btnVin:true
    })
  }
  btnVout = () => {
    this.setState({
      btnVin:false
    })
  }
  send = () => {
    let { name, danwei, zhiwei, ketizhu, mail, phone, address, server, simple, question } = this.state
    if (name === '') {
      window.msginfo('')
    } else if (danwei === '') {
      window.msginfo('')
    } else if (zhiwei === '') {
      window.msginfo('')
    } else if (ketizhu === '') {
      window.msginfo('')
    } else if (mail === '') {
      window.msginfo('')
    } else if (phone === '' || !regPhone(phone)) {
      window.msginfo('')
    } else if (address === '') {
      window.msginfo('')
    } else if (server === '') {
      window.msginfo('')
    } else if (simple === '') {
      window.msginfo('')
    } else  {
      api.senduser({ name: name, danwei: danwei, zhiwei: zhiwei, ketizhu: ketizhu, mail: mail, phone: phone, address: address, server: server, simple: simple, question: question }, (res) => {
        window.msgtip(res.msg)
        window.location.hash = '#/join/succeed/'
      })
    }
  }
}
export default Comp



