let {Get, Post} = require('./ajax')
let url = ''
let isDev = window.location.href.indexOf(':3000') > 0
if (isDev) {
  url = 'http://172.16.163.134:7000'
}
let api = {
  senduser: function (data={name: '',
  danwei: '',
  zhiwei: '',
  ketizhu: '',
  mail: '',
  phone: '',
  address: '',
  server: '',
  simple: '',
  question: '',}, event) {
    Post(url + '/api/senduser/', data, (res) => {
      if (res.status !== 200) {
        window.msgerr(res.msg)
      } else {
        event(res)
      }
    })
  }
}

export default api


