import React from 'react'
import MerginHeight from '../../comps/MerginHeight'

class Comp extends React.PureComponent {
  constructor(props) {
    super(props)
  }
  render() {
    let imgH = 120
    return (
      <div className='container-fluid' >
        <MerginHeight h={10} ></MerginHeight>
        <div className='row' style={{
          height: 50,
          lineHeight: '50px',
        }} >
          <span style={{ marginLeft: 10, color: '#35B69E', fontSize: 18 }} >GDP</span><span style={{ marginLeft: 20, fontSize: 12, fontWeight: 300, color: '#35b69e', opacity: 0.8 }} >BRIEF INTRODUCTION</span>
        </div>
        <div className='row' style={{
          height: 1, background: '#fff',
          boxShadow:'0px 1px 0px #35B69E',
        }} ></div>
        <div className='row' style={{
          paddingLeft: '10px',
          paddingRight:'10px',
          marginTop:'10px'
        }} >
          <MerginHeight h={10}></MerginHeight>
          <p className='h5' >GDP(Genome Decode Program),</p>  
          <p className='p' >ENCODE1%DNADNA</p>
          <p className='p' >BGI Online</p>
          <p className='p' >201710</p>
        </div>
        <div className='row' >
          <div className='col-xs-1' style={{
            height: imgH, width: '31%',
            marginLeft:'3%',
            backgroundImage: `url(./icon/img1.png)`,
          backgroundSize: `cover`,
          backgroundPosition: `50% 50%`
          }} ></div>
          <div className='col-xs-1' style={{
            height: imgH, width: '31%',
            marginLeft:'2%',
            backgroundImage: `url(./icon/img2.png)`,
          backgroundSize: `cover`,
          backgroundPosition: `50% 50%`
          }} ></div>
          <div className='col-xs-1' style={{
            height: imgH, width: '31%',
            marginLeft:'2%',
            backgroundImage: `url(./icon/img3.png)`,
          backgroundSize: `cover`,
          backgroundPosition: `50% 50%`
          }} ></div>
          <div className='row' style={{ height: '20px' }} >
          <div className='col-xs-1' style={{height:'100%'}} ></div>  
          </div>
        </div>
      </div>
    )
  }
}
export default Comp


